<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">汇总数据</span>
					<!--<span class="ts">提示：收入=认购服务费+管理费</span>-->
				</div>
			</div>
			<div class="cash_flow_table table">
				<el-table ref="multipleTable" :data="totalData" border>
					<el-table-column prop="coin" label="币种" width="100">
						<template slot-scope="scope">
							{{ scope.row.unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column  prop="invest" label="投资金额（万元）">
					</el-table-column>
					<el-table-column prop="return_money" label="回款本金（万元）">
					</el-table-column>
					<el-table-column prop="return_money_taxed" label="回款收益-税后（万元）">
					</el-table-column>
					<el-table-column prop="return_money_total" label="回款总额（万元）">
					</el-table-column>
					<el-table-column prop="income" label="收入（万元）">
					</el-table-column>
					<el-table-column prop="output" label="支出（万元）">
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
			</div>
		</div>
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">现金流数据</span>
					<el-select v-model="cashFlowType" placeholder="类型" class=" select_border" style="width:140px;margin-left:30px;" clearable @change="getDetailData(1)">
						<el-option v-for="item in type_list" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
					<div class="search_box">
						<el-input v-model="cashFlowEvent" prefix-icon="el-icon-search" placeholder="输入事件名称" clearable>
						</el-input>
					</div>
					<div class="btn_box">
						<button @click="getDetailData(1)">搜索</button>
					</div>
				</div>
				<div class="flex_start">
					<!-- <div class="table-group-title-btn btn-blue flex_start_right" @click="DropboxShow">
						<span class="iconfont icon-jia"></span>添加新的现金流
						<div class="cash_flow_dropbox " :class="{'cash_flow_dropbo_show':cashFlowDropboxShow}" >
							<div class="cash_flow_dropbox_item1"></div>
							<div class="cash_flow_dropbox_item flex_start " @click="toAdd(1)" >
								<span class="iconfont icon-shouru"></span>收入-管理费
							</div>
							<div class="cash_flow_dropbox_item flex_start" @click="toAdd(3)" >
								<span class="iconfont icon-zhichu"></span>支出
							</div>
							<div class="cash_flow_dropbox_item1" ></div>
						</div>
					</div> -->
					<el-dropdown trigger="click" @command="toAdd">
						<span class="el-dropdown-link">
							<span class="iconfont icon-jia"></span>添加新的现金流
						</span>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item command="3"><span class="iconfont icon-zhichu "></span><span class="dropdown_item_name">支出</span></el-dropdown-item>
							<el-dropdown-item command="1"><span class="iconfont icon-shouru"></span><span class="dropdown_item_name">收入-管理费</span></el-dropdown-item>
							
						</el-dropdown-menu>
					</el-dropdown>
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="table-normal-box table">
					<el-table ref="multipleTable" :data="detailData" border style="width: 100%" max-height="350">
						<el-table-column sortable label="序号" width="100">
							<template slot-scope="scope">
								{{ scope.$index + 1 }}
							</template>
						</el-table-column>
						<el-table-column sortable prop="date" label="日期" width="150">
						</el-table-column>
						<el-table-column sortable prop="type" label="类别" width="150">
							<template slot-scope="scope">
								<div class="" v-if="scope.row.type == 1">
									收入-管理费
								</div>
								<div class="" v-else-if="scope.row.type == 2">
									收入-认购服务费
								</div>
								<div class="" v-else-if="scope.row.type == 3">
									支出
								</div>
								<div class="" v-else-if="scope.row.type == 4">
									投资
								</div>
								<div class="" v-else>
									回款
								</div>
							</template>
						</el-table-column>
						<el-table-column sortable prop="event" label="事件" width="150">
						</el-table-column>
						<el-table-column sortable prop="fee" label="金额（万元）" width="180">
						</el-table-column>
						<el-table-column sortable prop="count_fee" label="应计金额（万元）" width="180">
						</el-table-column>
						<el-table-column sortable prop="unit" label="币种" width="150">
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<el-table-column sortable prop="note" label="备注" width="150">
						</el-table-column>
						<el-table-column fixed="right" label="操作" width="150">
							<template slot-scope="scope">
								<el-button @click="viewCash(scope.row)" type="text" size="small">查看</el-button>
								<el-button v-if="scope.row.is_del" @click="deleteCompanyRunCashDetail(scope.row)" type="text" size="small">删除</el-button>
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
					<div class="" v-if="total">
						<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    						</el-pagination>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import {   getCompanyRunCashTotalApi,getCompanyRunCashDetailApi,deleteCompanyRunCashDetailApi    } from '../../config/api'
	
	export default {
		inject: ['reload'],
		data() {
			return {
				id: '',
				totalData: [],
				detailData: [],
				cashFlowType: '', //现金流类型
				cashFlowEvent: '' ,//现金流类型事件
				
				currentPage: 1,
				pageSize: 10,
				total: 0,
				type_list: [
					{
						value: 1,
						label: '收入-管理费'
					},
					{
						value: 2,
						label: '收入-认购服务费'
					},
					{
						value: 3,
						label: '支出'
					},
					{
						value: 4,
						label: '投资'
					},
					{
						value: 5,
						label: '回款'
					},
				],
				cashFlowDropboxShow: false,
				no_power_text: '您暂无权限进行此操作！',
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getTotalData()
			this.getDetailData()
		},
		computed: {
	
		},
		mounted() {},
		methods: {
			getTotalData(){
				getCompanyRunCashTotalApi({
					sg_company_manager_id: this.id,
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.totalData = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getDetailData(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				getCompanyRunCashDetailApi({
					sg_company_manager_id: this.id,
					page: this.currentPage,
					size: this.pageSize,
					type: this.cashFlowType,
					search_content: this.cashFlowEvent
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.detailData = res.data.payload.data;
						this.total = res.data.payload.count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			deleteCompanyRunCashDetail(e){
				if (e.is_del) {
					var message = "确定要删除该条数据吗"
					dialogConfirmHasbtn(message).then(() => {
						deleteCompanyRunCashDetailApi({
							type: e.type,
							count: e.count
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.getTotalData()
								
								let length = this.detailData.length;
								if(length > 1){
									this.getDetailData();
								}else{
									if(this.currentPage > 1){
										this.currentPage = this.currentPage - 1;
										this.getDetailData();
									}else{
										this.getDetailData(this.currentPage);
									}
								}
								
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}).catch(() => {});
				} else{
					dialogConfirm("info", this.no_power_text);
				}
			},
			// makeTotalData(arr){
			// 	this.totalData[0].invest_money = arr[4][1];
			// 	this.totalData[0].back_money = arr[5][1];
			// 	this.totalData[0].back_income = arr[6][1];
			// 	this.totalData[0].back_total = arr[7][1];
			// 	this.totalData[0].income = arr[0][1];
			// 	this.totalData[0].pay = arr[3][1];
			// 	this.totalData[1].invest_money = arr[4][2];
			// 	this.totalData[1].back_money = arr[5][2];
			// 	this.totalData[1].back_income = arr[6][2];
			// 	this.totalData[1].back_total = arr[7][2];
			// 	this.totalData[1].income = arr[0][2];
			// 	this.totalData[1].pay = arr[3][2];
			// },
			// 分页功能模块
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getDetailData()
			},
			changeCurrentPage(e){
				this.currentPage = e;
				this.getDetailData()
			},
			viewCash(row){
				//console.log(row);
				let pageName = this.selectCashFlowType(row.type);
				this.$router.push({
					name: pageName,
					query: {
						id: this.id,
						count: row.count,
						type: row.inside_type,
						is_editable: row.is_del
					}
				})
			},
			deleteCash(index,e){
				//console.log(e);
			},
			
			
			dropSelect(item) {
				//console.log(item);
			},
			//顶部导航栏下拉框打开关闭
			DropboxShow() {
				this.cashFlowDropboxShow = !this.cashFlowDropboxShow;
			},
			toAdd(type){
				let pageName = this.selectCashFlowType(parseInt(type));
				this.$router.push({
					name: pageName,
					query: {
						id: this.id,
						add: 1,
					}
				})
			},
			selectCashFlowType(type){
				let pageName = '';
				switch(type){
					case 1:
						pageName = 'cashFlowIncomeManageMoney';//收入-管理费
						break;
					case 2:
						pageName = 'cashFlowIncomeServiceMoney';//收入-认购服务费
						break;
					case 3:
						pageName = 'cashFlowPay';//支出
						break;
					case 4:
						pageName = 'cashFlowInvest';//投资
						break;
					case 5:
						pageName = 'cashFlowBackMoney';//回款
						break;
				}
				return pageName;
			}
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		/*padding: 0 30px 30px 30px;*/
	}
	
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}





	.cash_flow_dropbox {
		position: absolute;
		border: 1px solid #E3EAEF;
		visibility: hidden;
		transition: all 0.3s;
		top: 30px;
		left: 50%;
		transform: translateX(calc(-50% + 7px));
		opacity: 0;
		box-shadow: 0px 0px 4px 2px rgba(128, 128, 128, 0.1);
		border-radius: 10px;
		z-index: 100;
	}

	.cash_flow_dropbo_show {
		visibility: visible;
		opacity: 1;
		top: 40px;
		left: 50%;
  		transform: translateX(calc(-50% + 7px));
	}
	.cash_flow_dropbox_item {
		width: 180px;
		height: 36px;
		font-size: 14px;
		background: #fff;
		color: #545454;
		position: relative;
		padding-left: 30px;
		&:hover {
			background: #F3F6FB;
			cursor: pointer;
		}
		.iconfont{
			font-size: 16px;
			margin-right: 15px;
		}
		.icon-jiaokuan{
			color: #72D39D;
		}
		.icon-touzi{
			color: #ED7B5C;
		}
		.icon-huikuan{
			color: #5AC8FA;
		}
		.icon-fenpei{
			color: #6292F4;
		}
		.icon-shouru{
			color: #5AC8FA;
		}
		.icon-zhichu{
			color: #6292F4;
		}
	}
	.cash_flow_dropbox_item1{
		background-color: #fff;
		height: 15px;
		width: 180px;
	}
	.flex_start_right{
		position: relative;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-left: 13px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
</style>


